<template>
  <div class="forms">
    <h1>{{ msg }}</h1>
    <!--<p>Welcome to the forms page</p>-->
    <my-survey/>
  </div>
</template>

<script>
import MySurvey from '@/components/MySurvey'

export default {
  name: 'Forms',
  components: { MySurvey },
  data () {
    return {
      msg: 'Forms Page'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



